<template>
    <div class="Loding">
        <img :src="Wallpaper3" alt="">
    </div>
</template>

<script>
import Wallpaper from '../../public/static/img/loding.jpg';
import Wallpaper1 from '../../public/static/img/loding1.jpg';
import Wallpaper2 from '../../public/static/img/loding2.jpg';
import Wallpaper3 from '../../public/static/img/loding3.jpg';
import { ElLoading } from 'element-plus';

export default {
    name:'Loding',
    data(){
        return{
            Wallpaper,
            Wallpaper1,
            Wallpaper2,
            Wallpaper3,
            userInfo: this.$token.verify(localStorage.getItem("token")).decoded.data,
        }
    },
    mounted(){
        // 加载动画
        ElLoading.service({
            lock: true,
            text: '加载中...',
            background: 'rgba(0, 0, 0, 0.7)'
        })

        setTimeout(()=>{
            if(this.userInfo.admin){
                this.$router.push('/admin');
            }else{
                this.$router.push('/user');
            }
        },2000)
    }
}
</script>

<style scoped lang="scss">
.Loding{
    width: 100%;
    height: 100%;
    img{
        width: 100%;
        height: 100%;
        object-fit:cover;
    }
}
</style>